import { Lyric } from '../models/models';
import data from './db/data';

const service = {
  getAll: (): Promise<Lyric[]> => {
    return Promise.resolve(data.lyrics);
  },
  findById: (id: number): Promise<Lyric | undefined> => {
    const found = data.lyrics.find((lyric) => lyric.number === id);
    return Promise.resolve(found);
  },
  search: (search: string): Promise<Lyric[]> => {
    const s = String(search).toLowerCase();
    const filtered = data.lyrics.filter(
      (lyric) =>
        String(lyric.number).includes(s) ||
        String(lyric.author).toLowerCase().includes(s) ||
        String(lyric.title).toLowerCase().includes(s)
    );

    return Promise.resolve(filtered);
  },
};

export default service;
